.bar-tooltip {	
    text-align: center;			
    width: 60px;					
    height: 20px;					
    padding: 2px;				
    font: 12px sans-serif;		
    background: lightsteelblue;	
    border: 0px;		
    border-radius: 8px;			
    pointer-events: none;	
    z-index: 999;
    position: absolute;
    visibility: hidden;
}		